module.exports = "/* Magnific Popup CSS */\n\n.mfp-open,\n.mfp-open:focus {\n    outline: none;\n}\n\n.mfp-bg {\n    top: 0;\n    left: 0;\n    width: 100%;\n    height: 100%;\n    z-index: 1042;\n    overflow: hidden;\n    position: fixed;\n    background: #0b0b0b;\n    opacity: 0.8;\n    filter: alpha(opacity=80);\n}\n\n.mfp-wrap {\n    top: 0;\n    left: 0;\n    width: 100%;\n    height: 100%;\n    z-index: 1043;\n    position: fixed;\n    outline: none !important;\n    -webkit-backface-visibility: hidden;\n}\n\n.mfp-container {\n    text-align: center;\n    position: absolute;\n    width: 100%;\n    height: 100%;\n    left: 0;\n    top: 0;\n    padding: 0 8px;\n    box-sizing: border-box;\n}\n\n.mfp-container:before {\n        content: '';\n        display: inline-block;\n        height: 100%;\n        vertical-align: middle;\n    }\n\n.mfp-align-top .mfp-container:before {\n    display: none;\n}\n\n.mfp-content {\n    position: relative;\n    display: inline-block;\n    vertical-align: middle;\n    margin: 0 auto;\n    text-align: left;\n    z-index: 1045;\n}\n\n.mfp-inline-holder .mfp-content,\n.mfp-ajax-holder .mfp-content {\n    width: 100%;\n    cursor: auto;\n}\n\n.mfp-ajax-cur {\n    cursor: progress;\n}\n\n.mfp-zoom-out-cur, .mfp-zoom-out-cur .mfp-image-holder .mfp-close {\n    cursor: zoom-out;\n}\n\n.mfp-zoom {\n    cursor: pointer;\n    cursor: zoom-in;\n}\n\n.mfp-auto-cursor .mfp-content {\n    cursor: auto;\n}\n\n.mfp-close,\n.mfp-arrow,\n.mfp-preloader,\n.mfp-counter {\n    -webkit-user-select: none;\n    -moz-user-select: none;\n    -ms-user-select: none;\n        user-select: none;\n}\n\n.mfp-loading.mfp-figure {\n    display: none;\n}\n\n.mfp-hide {\n    display: none !important;\n}\n\n.mfp-preloader {\n    color: #CCC;\n    position: absolute;\n    top: 50%;\n    width: auto;\n    text-align: center;\n    margin-top: -0.8em;\n    left: 8px;\n    right: 8px;\n    z-index: 1044;\n}\n\n.mfp-preloader a {\n        color: #CCC;\n    }\n\n.mfp-preloader a:hover {\n            color: #FFF;\n        }\n\n.mfp-s-ready .mfp-preloader {\n    display: none;\n}\n\n.mfp-s-error .mfp-content {\n    display: none;\n}\n\nbutton.mfp-close,\nbutton.mfp-arrow {\n    overflow: visible;\n    cursor: pointer;\n    background: transparent;\n    border: 0;\n    -webkit-appearance: none;\n    display: block;\n    outline: none;\n    padding: 0;\n    z-index: 1046;\n    box-shadow: none;\n    color: white;\n}\n\nbutton::-moz-focus-inner {\n    padding: 0;\n    border: 0;\n}\n\n.mfp-close {\n    width: 64px;\n    /*height: 73px;*/\n    line-height: 44px;\n    position: absolute;\n    right: 0;\n    top: 0;\n    text-decoration: none;\n    text-align: center;\n    opacity: 0.65;\n    filter: alpha(opacity=65);\n    padding: 0 0 18px 10px;\n    color: #FFF;\n    font-style: normal;\n    font-size: 28px;\n    font-family: Arial, Baskerville, monospace;\n}\n\n.mfp-close:hover,\n    .mfp-close:focus {\n        opacity: 1;\n        filter: alpha(opacity=100);\n    }\n\n.mfp-close:active {\n        top: 1px;\n    }\n\n.mfp-close-btn-in .mfp-close {\n    color: #333;\n}\n\n.mfp-image-holder .mfp-close,\n.mfp-iframe-holder .mfp-close {\n    color: #FFF;\n    /*right: -6px;*/\n    text-align: right;\n    /*padding-right: 6px;*/\n    width: 100%;\n}\n\n.mfp-counter {\n    position: absolute;\n    top: 0;\n    right: 0;\n    color: #CCC;\n    font-size: 12px;\n    line-height: 18px;\n    white-space: nowrap;\n}\n\n.mfp-arrow {\n    position: absolute;\n    opacity: 0.65;\n    filter: alpha(opacity=65);\n    margin: 0;\n    top: 50%;\n    margin-top: -55px;\n    padding: 0;\n    width: 90px;\n    height: 110px;\n    -webkit-tap-highlight-color: transparent;\n}\n\n.mfp-arrow:active {\n        margin-top: -54px;\n    }\n\n.mfp-arrow:hover,\n    .mfp-arrow:focus {\n        opacity: 1;\n        filter: alpha(opacity=100);\n    }\n\n.mfp-arrow-left {\n    left: 0;\n}\n\n.mfp-arrow-left:after,\n    .mfp-arrow-left .mfp-a {\n        margin-left: 31px;\n        margin-left: 39px;\n        content: \"\\f104\";\n        display: inline-block;\n        font: normal normal normal 14px/1 FontAwesome;\n        font-size: 78px;\n        text-rendering: auto;\n        -webkit-font-smoothing: antialiased;\n        -moz-osx-font-smoothing: grayscale;\n    }\n\n.mfp-arrow-right {\n    right: 0;\n}\n\n.mfp-arrow-right:after,\n    .mfp-arrow-right .mfp-a {\n        /* border-left: 17px solid #FFF; */\n        margin-left: 39px;\n        content: \"\\f105\";\n        display: inline-block;\n        font: normal normal normal 14px/1 FontAwesome;\n        font-size: 78px;\n        text-rendering: auto;\n        -webkit-font-smoothing: antialiased;\n        -moz-osx-font-smoothing: grayscale;\n    }\n\n.mfp-iframe-holder {\n    padding-top: 40px;\n    padding-bottom: 40px;\n}\n\n.mfp-iframe-holder .mfp-content {\n        line-height: 0;\n        width: 100%;\n        max-width: 1200px;\n        max-width: 80%;\n    }\n\n.mfp-iframe-holder .mfp-close {\n        top: -40px;\n    }\n\n.mfp-iframe-scaler {\n    width: 100%;\n    height: 0;\n    overflow: hidden;\n    padding-top: 56.25%;\n}\n\n.mfp-iframe-scaler iframe {\n        position: absolute;\n        display: block;\n        top: 0;\n        left: 0;\n        width: 100%;\n        height: 100%;\n        box-shadow: 0 0 8px rgba(0, 0, 0, 0.6);\n        background: #000;\n    }\n\n/* Main image in popup */\n\nimg.mfp-img {\n    width: auto;\n    max-width: 100%;\n    height: auto;\n    display: block;\n    line-height: 0;\n    box-sizing: border-box;\n    padding: 40px 0 40px;\n    margin: 0 auto;\n}\n\n/* The shadow behind the image */\n\n.mfp-figure {\n    line-height: 0;\n}\n\n.mfp-figure:after {\n        content: '';\n        position: absolute;\n        left: 0;\n        top: 40px;\n        bottom: 40px;\n        display: block;\n        right: 0;\n        width: auto;\n        height: auto;\n        z-index: -1;\n        box-shadow: 0 0 8px rgba(0, 0, 0, 0.6);\n        background: #444;\n    }\n\n.mfp-figure small {\n        color: #BDBDBD;\n        display: block;\n        font-size: 12px;\n        line-height: 14px;\n    }\n\n.mfp-figure figure {\n        margin: 0;\n    }\n\n.mfp-bottom-bar {\n    margin-top: -36px;\n    position: absolute;\n    top: 100%;\n    left: 0;\n    width: 100%;\n    cursor: auto;\n}\n\n.mfp-title {\n    text-align: left;\n    line-height: 18px;\n    color: #F3F3F3;\n    word-wrap: break-word;\n    padding-right: 36px;\n}\n\n.mfp-image-holder .mfp-content {\n    max-width: 100%;\n}\n\n.mfp-gallery .mfp-image-holder .mfp-figure {\n    cursor: pointer;\n}\n\n@media screen and (max-width: 800px) and (orientation: landscape), screen and (max-height: 300px) {\n    /**\n       * Remove all paddings around the image on small screen\n       */\n    .mfp-img-mobile .mfp-image-holder {\n        padding-left: 0;\n        padding-right: 0;\n    }\n\n    .mfp-img-mobile img.mfp-img {\n        padding: 0;\n    }\n\n    .mfp-img-mobile .mfp-figure:after {\n        top: 0;\n        bottom: 0;\n    }\n\n    .mfp-img-mobile .mfp-figure small {\n        display: inline;\n        margin-left: 5px;\n    }\n\n    .mfp-img-mobile .mfp-bottom-bar {\n        background: rgba(0, 0, 0, 0.6);\n        bottom: 0;\n        margin: 0;\n        top: auto;\n        padding: 3px 5px;\n        position: fixed;\n        box-sizing: border-box;\n    }\n\n        .mfp-img-mobile .mfp-bottom-bar:empty {\n            padding: 0;\n        }\n\n    .mfp-img-mobile .mfp-counter {\n        right: 5px;\n        top: 3px;\n    }\n\n    .mfp-img-mobile .mfp-close {\n        top: 0;\n        right: 0;\n        width: 35px;\n        height: 35px;\n        line-height: 35px;\n        background: rgba(0, 0, 0, 0.6);\n        position: fixed;\n        text-align: center;\n        padding: 0;\n    }\n}\n\n@media all and (max-width: 900px) {\n    .mfp-arrow {\n        -webkit-transform: scale(0.75);\n        transform: scale(0.75);\n    }\n\n    .mfp-arrow-left {\n        -webkit-transform-origin: 0;\n        transform-origin: 0;\n    }\n\n    .mfp-arrow-right {\n        -webkit-transform-origin: 100%;\n        transform-origin: 100%;\n    }\n\n    .mfp-container {\n        padding-left: 6px;\n        padding-right: 6px;\n    }\n}\n\n.mfp-ie7 .mfp-img {\n    padding: 0;\n}\n\n.mfp-ie7 .mfp-bottom-bar {\n    width: 600px;\n    left: 50%;\n    margin-left: -300px;\n    margin-top: 5px;\n    padding-bottom: 5px;\n}\n\n.mfp-ie7 .mfp-container {\n    padding: 0;\n}\n\n.mfp-ie7 .mfp-content {\n    padding-top: 44px;\n}\n\n.mfp-ie7 .mfp-close {\n    top: 0;\n    right: 0;\n    padding-top: 0;\n}\n\n/**\n* Fade-zoom animation for first dialog\n*/\n\n/* start state */\n\n.my-mfp-zoom-in {\n    opacity: 0;\n    transition: all 0.2s ease-in-out;\n    -webkit-transform: scale(0.8);\n    transform: scale(0.8);\n}\n\n/* animate in */\n\n.my-mfp-zoom-in.mfp-ready {\n    opacity: 1;\n    -webkit-transform: scale(1);\n    transform: scale(1);\n}\n\n/* animate out */\n\n.my-mfp-zoom-in.mfp-removing {\n    -webkit-transform: scale(0.8);\n    transform: scale(0.8);\n    opacity: 0;\n}\n\n/* Dark overlay, start state */\n\n.my-mfp-zoom-in.mfp-bg {\n    opacity: 0;\n    transition: opacity 0.3s ease-out;\n}\n\n/* animate in */\n\n.my-mfp-zoom-in.mfp-ready.mfp-bg {\n    opacity: 1;\n    background: #fff;\n    background: #000;\n    opacity: 0.8;\n}\n\n/* animate out */\n\n.my-mfp-zoom-in.mfp-removing.mfp-bg {\n    opacity: 0;\n}\n"